<template>
  <div id="d-table-test">
    <div style="width: 100%; height: 100%; float: left">
      <table-component
        titleName="标题"
        componentKey="tableKey1"
        :loading="loading"
        ref="dtable"
        :tableColumns="tableColumns"
        :tableData="tableData"
        :userPolicy="userPolicy"
        :currentUserPolicy="currentPolicy"
        :QueryComplate="QueryComplate"
        :UserPolicyComplate="UserPolicyComplate"
        :ChangeUserPolicy="ChangeUserPolicy"
        :SortVisible="true"
        :enums="enums"
        :total="total"
        :RowClick="RowClick"
        :SelectionChange="SelectionChange"
        :DefaultFirstRow="true"
        :AddVisible="true"
        :EditVisible="true"
        :DelVisible="true"
        :OperateColumnVisible="true"
        :AddData="AddDataFun"
        :EditData="EditDataFun"
        :DelRow="delRowFun"
        :DelRows="delRowsFun"
        rowKey="id"
        rowChildren="children"
      >
        <template slot="column" slot-scope="scope">
          <el-checkbox
            v-model="scope.row.col06"
            true-label="YES"
            false-label="NO"
          ></el-checkbox>
        </template>

        <template slot="header">
          <el-button @click="click1"> 插槽按钮 </el-button>
        </template>
      </table-component>
    </div>
  </div>
</template>
<script>
import { config1, config2 } from "./configData/config.js";
export default {
  name: "DTABLE",
  components: {},
  data() {
    return {
      tableColumns: [
        {
          prop: "col01",
          label: "数字列",
          dataType: "NUMERICAL",
          width: "80",
          align: "center",
          fixed: 0,
          sortBy: 1,
          showType: "TEXT",
          fshow: 1,
        },
        {
          prop: "col02",
          label: "字符串列",
          dataType: "CHARACTER",
          width: "120",
          align: "center",
          fixed: 0,
          sortBy: 2,
          showType: "TEXT",
          fshow: 1,
        },
        {
          prop: "col03",
          label: "时间列",
          dataType: "DATE",
          width: "150",
          align: "center",
          fixed: 0,
          sortBy: 3,
          showType: "TEXT",
          fshow: 1,
        },
        {
          prop: "col05",
          label: "枚举列",
          dataType: "CHARACTER",
          width: "120",
          align: "center",
          fixed: 0,
          sortBy: 5,
          showType: "TEXT",
          fshow: 1,
          enumKey: "enumKeyName",
        },
        {
          prop: "col06",
          label: "作用域插槽列",
          dataType: "CHARACTER",
          width: "80",
          align: "center",
          fixed: 0,
          sortBy: 6,
          showType: "TEXT",
          fshow: 1,
          fscope: true,
        },
      ],
      tableData: [],
      userPolicy: [
        {
          label: "预设1",
          value: "1",
        },
        {
          label: "预设2",
          value: "2",
        },
      ],
      currentPolicy: {},
      enums: [
        {
          key: "enumKeyName",
          values: [
            {
              label: "男",
              value: "M",
            },
            {
              label: "女",
              value: "F",
            },
          ],
        },
      ],
      total: 1000000,
      loading: false,
    };
  },
  created() {
    this.GetUserPolicy();
    this.createData();
  },
  mounted() {
    this.currentPolicy = config1;
    this.$refs.dtable.InitParams();
  },
  methods: {
    QueryComplate(_screens, _sorts) {
      this.createData();
    },
    createData() {
      console.log("createData>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>");
      this.loading = true;
      new Promise((resolve, reject) => {
        setTimeout(() => {
          const arr = [];
          for (var i = 0; i < 100; i++) {
            arr.push({
              col01: i,
              col02: "string " + " -1",
              col03: Date.now() + Math.random() * 1000000000,
              col04: "2022-07-19 05:20:22",
              col05: Math.random() > 0.5 ? "F" : "M",
              col06: Math.random() > 0.5 ? "YES" : "NO",
              id: this.uuid() + Date.now() + Math.random() * 1000000000,
            });
          }
          this.tableData = arr;
          resolve();
        }, 1000);
      }).finally(() => {
        this.loading = false;
      });
    },
    uuid() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function (c) {
          var r = (Math.random() * 16) | 0,
            v = c == "x" ? r : (r & 0x3) | 0x8;
          return v.toString(16);
        }
      );
    },
    // 保存用户版本事件
    UserPolicyComplate(_userPolicy) {
      // 调用后台API进行数据保存
      this.$message.error("执行保存版本API");
    },
    // 获取用户版本配置
    GetUserPolicy() {
      // 获取菜单组件版本list
      // this.userPolicy = [];
      // this.userPolicy = [
      //   {
      //     label:"测试77777",
      //     value:"69a2c32f-cbc7-402d-a576-1c1f3ffaa9c7"
      //   },
      //   {
      //     label:"测试",
      //     value:"003b3120-bb35-4631-86db-6a908c4ddea6"
      //   }
      // ]
    },
    // 根据id获取版本配置
    ChangeUserPolicy(value) {
      console.log({ value });
      if (value == 1) {
        this.currentPolicy = config1;
      } else if (value == 2) {
        this.currentPolicy = config2;
      }
    },
    click1() {
      this.$message.error("这是按钮1自定义事件");
    },
    click2() {
      this.$message.error("这是按钮2自定义事件");
    },
    click3() {
      this.$message.error("这是按钮3自定义事件");
    },
    RowClick(row, column, event) {
      console.log("RowClick", row, column, event);
    },
    SelectionChange(selection) {
      console.log("SelectionChange", selection);
    },
    AddDataFun(tableData) {
      console.log("新建提交的表单数据：", tableData);
    },
    EditDataFun(tableData) {
      console.log("编辑提交的表单数据：", tableData);
    },
    delRowFun(row) {
      console.log("删除行：", row);
    },
    delRowsFun(rows) {
      console.log("删除多行：", rows);
    },
  },
};
</script>
<style>
#d-table-test {
  width: 100%;
  height: 100vh;
}
</style>
